import { TabsPage } from './../pages/tabs/tabs';
import { IndexPage } from './../pages/index/index';
import { DashboardPage } from './../pages/dashboard/dashboard';
import { IntroPage } from './../pages/intro/intro';
import { Component, ViewChild,  } from '@angular/core';
import { Platform, Nav, App } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import {SettingsProvider} from "../providers/settings/settings";
import { AboutPage } from '../pages/about/about';
import { Storage } from '@ionic/storage';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = 'IntroPage';

  @ViewChild(Nav) nav: Nav;
  selectedTheme: String;  //Themeoption
  activepage: any;

  pages: Array<{ title: string, component: string, icon: string, index:number }>  ;

  // navCtrl: any;

  constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen, private settings: SettingsProvider, private storage: Storage) {
    this.settings.getActiveTheme().subscribe(val => this.selectedTheme = val);  //Themeoption
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
    });

    this.pages =  [

    { title:'Find a Provider ', component:'ProviderPage',  icon:'ios-search', index:0.2, },
    { title:'Request a Report', component:'ReportsPage', icon:'ios-trending-up', index:0.2, },
    { title:'Log a Complaints', component:'ComplaintsPage', icon:'ios-repeat', index:0.2, },

    { title:'About HIIS', component:'AboutPage', icon:'ios-call', index:0.2, },
    { title:'Share', component:'AboutPage', icon:'ios-share', index:0.2, },
    { title:'Setting', component:'AboutPage', icon:'ios-settings', index:0.2, },



  ];
  this.activepage = this.pages[0];
}

openPage(page) {


  this.nav.setRoot(page.component);
  this.activepage = DashboardPage;

}
checkActive(page) {
  return page == this.activepage;
}

dashboard(){
  this.nav.setRoot(DashboardPage);
}


about(){
  this.nav.setRoot('TabsPage');


}


}





