import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { NavController, App } from 'ionic-angular';
import { Injectable, } from '@angular/core';

import { Storage } from '@ionic/storage';
import { Constants } from '../../models/constants';
import { UtilProvider } from '../util/util';

const PROVIDER = 'provider';

@Injectable()
export class AuthproviderProvider {

  private baseUrl = 'http://hiis.herokuapp.com/api/v1';

  constructor(
    private http: HttpClient,
    private storage: Storage,
    private utility: UtilProvider,
    private appCtrl: App) {}

  public login(email: string, password: string): Promise<any>{
    return this.http.post(`${this.baseUrl}/enroleeauth/sign_in`, {email:email, password:password})
      .toPromise()
      .then((provider) =>{
        console.log(provider);
        return this.storage.set(PROVIDER, provider);
      })
      .then(() => {
        return Promise.resolve();
      })
      .catch((error: HttpErrorResponse) => {
        console.log(error);
        return Promise.reject(error.statusText);
      });
  }

  public logout(navCtrl:NavController): void{
    this.utility.presentAlertConfirm('Confirm Logout..', 'Are you sure you want to <strong>Log out</strong> of this application?', () => {
      this.storage.remove(PROVIDER)
      .then(res => {
        this.appCtrl.getRootNav().setRoot('IntroPage');
      })
      .catch(err => {
        console.error(err);
      });
    });
  }

  public hasAuthentication(): Promise<boolean>{
    return this.storage.get(PROVIDER).then(res =>{
      if(res){
        return Promise.resolve(true);
      }else{
        return Promise.resolve(false);
      }
    });
  }

  public forgotPassword(email: string): Promise<any>{
    return this.http.post(`${this.baseUrl}/enroleeauth/password`, {email: email})
      .toPromise()
      .then(res =>{
        return Promise.resolve(res);
      })
      .catch((err: HttpErrorResponse) =>{
        return Promise.reject(err);
      });
  }

  // postData(credentials, type) {
  //   return new Promise((resolve, reject) => {
  //     //let headers = new Headers();

  //     this.http.post(apiUrl + type, credentials)
  //       .subscribe(res => {
  //         console.log("RESPONSE ",res)
  //         resolve(res);
  //       }, (err) => {
  //         console.log("ERROR", err)
  //         reject(err);
  //       });
  //   });
  // }

}



