import { AboutPage } from '../about/about';
import { Component } from '@angular/core';
import { NavController,   IonicPage, NavParams, ToastController, AlertController } from 'ionic-angular';
import { ClaimsPage } from '../claims/claims';
import { SettingsProvider } from '../../providers/settings/settings';
import { AbstractPage } from '../abstract/abstract';
import { Storage } from '@ionic/storage';
import { AuthproviderProvider } from './../../providers/authprovider/authprovider';



@IonicPage()
@Component({
  selector: 'page-dashboard2',
  templateUrl: 'dashboard2.html'
})
export class DashboardPage {

  nextPage = 'AboutPage';
  selectedTheme: string;


    constructor(
      public navCtrl: NavController,
      public navParams: NavParams,
      private settings: SettingsProvider,
      private storage: Storage,
      private auth: AuthproviderProvider,
      private AlertController:AlertController,
      ) {

    }


    ionViewDidLoad() {
      console.log('ionViewDidLoad Dashboard2Page');
    }

    toggleAppTheme() {
      if (this.selectedTheme === 'dark-themes') {
        this.settings.setActiveTheme('light-theme');
      } else {
        this.settings.setActiveTheme('dark-theme');
      }
    }

    goToAbstractPage(){
      this.navCtrl.push('AbstractPage');

  }

  public logout(): void {
    this.auth.logout(this.navCtrl);
  }



}



